@import '../../assets/styles/global';

.NewsCarousel {
  &-item {
    background: $white;

    &:hover {
      cursor: pointer;

      .NewsCarousel-item-info {
        &-title {
          color: $electric-violet;
        }
      }
    }

    &-image {
      flex: 0 0 50%;
      max-width: 50%;
      display: flex;
      height: 350px;

      @media (max-width: 768px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        margin: auto;
      }
    }

    &-info {
      padding: 3rem;
      flex: 0 0 50%;
      max-width: 50%;

      @media (max-width: 768px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      &-title {
        font-size: 3.2rem;
        font-weight: 700;
        line-height: 1.3;
        margin-bottom: 2rem;
        transition: $common-transition;
      }

      &-description {
        font-size: 1.8rem;
        color: $big-stone;
        margin-bottom: 1rem;
        height: 14rem;
        @include ellipsis(5);

        * {
          font-size: 1.8rem !important;
          margin: 0 !important;
        }
    
        img {
          display: none !important;
        }
      }

      &-share {
        color: $international-klein-blue;
        font-size: 1.6rem;

        .Icon {
          flex: 0 0 1.6rem;
          max-width: 1.6rem;
          margin-right: 1rem;
        }
      }
    }
  }
}